import { loadToys } from "../store/toy.action"
import { useSelector } from "react-redux"
import { dashboardService } from "../services/dashboard.service"
import { useEffect } from "react"

import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';
import { Doughnut } from 'react-chartjs-2';
ChartJS.register(ArcElement, Tooltip, Legend);


export function Dashboard() {
    const labels = useSelector(storeState => storeState.labels)
    const filterBy = useSelector(storeState => storeState.filterBy)

    useEffect(() => {
        loadToys(filterBy)
    }, [])
    const toys = useSelector(storeState => storeState.toys)

    const averagePrices = dashboardService.getPricesPerLabel(toys, labels)

    const chartData = {
        labels,
        datasets: [
            {
                label: 'Average price',
                data: averagePrices,
                backgroundColor: [
                    'rgba(255, 99, 132, 0.2)',
                    'rgba(54, 162, 235, 0.2)',
                    'rgba(255, 206, 86, 0.2)',
                    'rgba(75, 192, 192, 0.2)',
                    'rgba(153, 102, 255, 0.2)',
                    'rgba(255, 159, 64, 0.2)',
                ],
                borderColor: [
                    'rgba(255, 99, 132, 1)',
                    'rgba(54, 162, 235, 1)',
                    'rgba(255, 206, 86, 1)',
                    'rgba(75, 192, 192, 1)',
                    'rgba(153, 102, 255, 1)',
                    'rgba(255, 159, 64, 1)',
                ],
                borderWidth: 1,
            },
        ],
    }

    return (
        <section className="dashboard-container">
            <h1>Average prices per Label</h1>
            <div className="doughnut">
                <Doughnut data={chartData} />
            </div>
        </section>)
}


